.newgroup {

    padding: 0px; /* Поля вокруг текста */
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;

    box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);

    border-radius: 20px; /* Уголки */
}
.newgroup-button-light {

    padding: 0px; /* Поля вокруг текста */
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    background-color:white;
    box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    transition: 50ms ease all;

    border-radius: 20px; /* Уголки */
}
.newgroup-button-light:active{
    background-color:#E4E4E4;
    box-shadow:0px 1px 8px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 1px 8px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 1px 8px 0px rgba(0,0,0,0.16);
}
